/* STATIC STYLES (desktop-first) */

.glass-card3 {
  display: flex;
  flex-direction: column;
  align-items: center;

  width: 60vw;
  padding: 2em;

  overflow: hidden;
  border: 1px solid var(--color-primary-text-dark);
  border-radius: 1em;

  background-color: var(--color-card);
  backdrop-filter: blur(10px);
  opacity: 0.96;

  transition: transform 0.5s ease-in-out;

  gap: 1rem;
}

.glass-card3:hover {
  border: 1px solid var(--color-secondary-dark);

  opacity: 1;

  transform: scale(1.2);
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
  .glass-card3 {
    width: 70vw;
  }
}

@media (max-width: 992px) {
  .glass-card3 {
    width: 70vw;
  }
}

@media (max-width: 768px) {
  .glass-card3 {
    width: 80vw;
  }
}

@media (max-width: 576px) {
  .glass-card3 {
    width: 80vw;
  }
}
