@import './root-styles.css';
@import './root-utils.css';
@import './root-typography.css';
@import './utils.css';

/* STATIC STYLES (desktop-first) */

main {
  position: relative;

  min-height: 100vh;
}

.background-video {
  position: fixed;
  top: 0;
  left: 0;
  z-index: -2;

  width: 100vw;
  height: 100vh;

  object-fit: cover;
}

.backdrop-blur {
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;

  width: 100vw;
  height: 100vh;

  background: var(--color-blur);
  backdrop-filter: blur(7px);
}

.content {
  position: relative;
  z-index: 1;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.content > :nth-child(3) ~ * {
  margin-top: 30rem;
}

section {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 100vw;

  text-align: center;
  color: var(--color-primary-text);
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
  .content > :nth-child(3) ~ * {
    margin-top: 20rem;
  }
}

@media (max-width: 992px) {
  .content > :nth-child(3) ~ * {
    margin-top: 10rem;
  }
}

@media (max-width: 768px) {
  .content > :nth-child(3) ~ * {
    margin-top: 10rem;
  }
}

@media (max-width: 576px) {
  .content > :nth-child(3) ~ * {
    margin-top: 10rem;
  }
}
