/* STATIC STYLES (desktop-first) */

.glass-card1 {
  display: flex;
  flex-direction: column;
  align-items: center;

  width: 20vw;
  padding: 2em;

  border: 1px solid var(--color-primary-text-dark);
  border-radius: 1em;

  background-color: var(--color-card);
  backdrop-filter: blur(10px);

  gap: 1rem;
}

.glass-card1:hover {
  border: 1px solid var(--color-secondary-dark);
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
  .glass-card1 {
    width: 25vw;
  }
}

@media (max-width: 992px) {
}

@media (max-width: 768px) {
  .glass-card1 {
    width: 75vw;
  }
}

@media (max-width: 576px) {
  .glass-card1 {
    width: 90vw;
  }
}
